<template>
  <div>
    <div class="header">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-luntanzixun" />
      </svg>
      <div class="mui-input-row mui-search">
        <input type="search" class="search mui-input-clear" placeholder="请输入关键字" />
      </div>
      <span class="mui-icon mui-icon-plus"></span>
    </div>
    <div id="slider" class="mui-slider">
      <div
        id="sliderSegmentControl"
        class="mui-slider-indicator mui-segmented-control mui-segmented-control-inverted"
      >
        <a class="mui-control-item mui-active" href="#item1mobile">热门</a>
        <a class="mui-control-item" href="#item2mobile">提问帖</a>
        <a class="mui-control-item" href="#item3mobile">分享帖</a>
      </div>

      <div id="sliderProgressBar" class="mui-slider-progress-bar mui-col-xs-4"></div>

      <div class="mui-slider-group">
        <div
          id="item1mobile"
          class="md-f1 mui-slider-item mui-control-content detailInfos md-box md-ver mui-active"
        >
          <Hot></Hot>
        </div>
        <div
          id="item2mobile"
          class="bg md-f1 mui-slider-item mui-control-content detailInfos1 md-box md-ver"
        >
        
          <Question></Question>
        </div>
        <div
          id="item3mobile"
          class="bg md-f1 mui-slider-item mui-control-content detailInfos2 md-box md-ver"
        >
        
          <Share></Share>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Hot from "./hot";
import Question from "./question";
import Share from "./share";
export default {
  components: {
    Hot,
    Question,
    Share
  },
  mounted() {
    mui(".mui-input-row input").input();
    mui(".mui-slider").slider();
  }
};
</script>
<style scoped>
.header {
  height: 60px;
  background: #6e8b3d;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.search {
  background: white;
  margin-bottom: 0px;
}
.mui-search {
  height: 34px;
  line-height: 34px;
}
.icon {
  font-size: 28px;
}
.mui-icon-plus {
  color: white;
  font-size: 20px;
}
.forum-item {
  display: flex;
  justify-content: space-between;
  background: white;
  padding-top: 15px;
  border-bottom: 1px gray solid;
}
.item-title {
  display: flex;
  flex-direction: column;
  font-weight: bolder;
  font-size: 18px;
}
.left {
  display: flex;
}
.right {
  display: flex;
  align-items: center;
  padding-right: 10px;
  margin-right: 10px;
}
.num {
  color: rgb(175, 4, 4);
  font-size: 16px;
  margin-left: 10px;
  margin-right: 10px;
}
.user-avatar {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
}
.user-info {
  margin-top: 10px;
  display: flex;
  align-items: center;
}
.item-des {
  margin-top: 10px;
  height: 40px;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-right: 10px;
}
.des-img {
  height: 80px;
  width: 120px;
}
.icon-zan {
  font-size: 10px;
}
.datetime {
  color: gray;
  font-size: 10px;
  margin-right: 10px;
}
.mui-segmented-control.mui-segmented-control-inverted
  ~ .mui-slider-progress-bar {
  background-color: #6e8b3d;
}
.mui-active {
  color: #6e8b3d !important;
}
 .mui-slider-group {
  height: 100vh;
}
.mui-slider-indicator{
    background-color: white;
}
/* .mui-slider .mui-slider-group {
  white-space: normal;
} */
/*.mui-control-content {
  background-color: white;
  min-height: 215px;
}
.mui-control-content .mui-loading {
  margin-top: 50px;
} */
</style>


